<template>
	<div class="position-relative" style="z-index: 1;">
		
		<transition name="fade">
		    <tip v-if="!show" :tipMessage="tipMessage"></tip>
		</transition>
		
		<el-row :gutter="10">
		  <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">称呼：<input v-model="ch" class="bg-fuzzy" type="text" placeholder="请输入称呼" style="border-style: none; width: 18.75rem;height: 2.5rem; margin: 15px 0px;"></el-col>
		  <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">电话：<input v-model="phone" class="bg-fuzzy" type="text" placeholder="请输入电话" style="border-style: none;width: 18.75rem;height: 2.5rem; margin: 15px 0px;"></el-col>
		  <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8">邮箱：<input v-model="email" class="bg-fuzzy" type="text" placeholder="请输入邮箱" style="border-style: none;width: 18.75rem;height: 2.5rem; margin: 15px 0px;"></el-col>

		</el-row>
		<div><lable>留言内容:</lable></div>
		<!-- 留言内容:<input v-model="messages" class="bg-fuzzy" type="text"  placeholder="请输入内容" style="border-style: none;height: 5.5rem;width: 18.75rem; margin: 15px 0px;"> -->
		<textarea v-model="messages" class="bg-fuzzy" placeholder="请输入内容"  name="" id="" cols="30" rows="10" style="border-style: none;height: 200px;width: 100%; margin: 15px 0px;"></textarea>
		
		<button @click="message()" style="width: 100%;height: 2.75rem;margin: 15px 0px;background-color: #FFC107;border-radius: 5px;border-style: none;font-size: 20px;font-weight: bolder;">留言</button>
	</div>
</template>

<script>
	const querystring = require('querystring');
	import tip from "./tip.vue"
	export default{
		components: {
			tip
		},
		data() {
			return {
				ch: "",
				phone:"",
				email:"",
				messages:"",
				show: true,
				tipMessage:"留言成功"
			}
		},
		computed: {
			checkPhone(){
				var myreg=/^[1][3,4,5,7,8][0-9]{9}$/;
				if (!myreg.test(this.phone)) {
					return false;
				} else {
					return true;
				}
			},
			checkEmail(){
				var myreg = /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/;
				   if (!myreg.test(this.email)) {
				       return false;
				   }
				   else {
				       return true;
				   }
			},
		},
		methods: {
			
			async message() {
				
				let that = this;
				if(!this.ch||!this.phone||!this.email||!this.messages){
					alert("输入框不能为空")
				}else{
					if(this.checkPhone && this.checkEmail){
						await this.$A.post("/api/insertMessage",querystring.stringify({
							"name":that.ch,
							"phone":that.phone,
							"email":that.email,
							"messages":that.messages,
							"time": Date.now()
						})).then(res =>{
							console.log(res)
							  this.show=false
							  setTimeout(()=>{
								this.show=true
							  },3000)
						}).catch((error)=>{
								console.log(error)
							}
						)
					}else{
						alert("手机或者邮箱格式不正确")
					}
					
					
					
				}
			}
		},
	}
</script>

<style>
	
	.fade-enter-active, .fade-leave-active {
	  transition: opacity .5s;
	}
	.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
	  opacity: 0;
	}
</style>
